.contact .box-contanier {
    display: flex;
    position: relative;
    z-index: 1000;
    min-width: 1100px;
    min-height: 550px;
}

.contact .box-contanier .contact-info {
    position: absolute;
    top: 40px;
    width: 350px;
    height: calc(100% - 80px);
    background-color: var(--heading-color);
    z-index: 1;
    padding: 40px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    box-shadow: 0 20px 25px rgba(0, 0, 0, 0.15);
}

.contact .box-contanier .contact-info h2 {
    color: #fff;
    font-size: 24px;
    font-weight: 500;
}

.contact .box-contanier .contact-info ul.info {
    position: relative;
    margin: 20px 0;
}

.contact .box-contanier .contact-info ul.info li {
    position: relative;
    display: flex;
    margin: 20px 0;
    cursor: pointer;
    align-items: flex-start;
}

.contact .box-contanier .contact-info ul.info li span:nth-child(1) {
    width: 30px;
    max-width: 30px;
} 

.contact .box-contanier .contact-info ul.info li span:nth-child(1) img {
    max-width: 100%;
    filter: invert(1);
    -webkit-filter: invert(1);
}

.contact .box-contanier .contact-info ul.info li span:nth-child(2) {
    color: #fff;
    margin-left: 10px;
    font-weight: 300;
}

.contact .box-contanier .contact-info ul.social {
    display: flex;
    position: relative;
    cursor: pointer;
}

.contact .box-contanier .contact-info ul.social li {
    margin-right: 15px;
}

.contact .box-contanier .contact-info ul.social li img {
    filter: invert(1);
    -webkit-filter: invert(1);
}

.contact .box-contanier .contact-form {
    position: absolute;
    padding: 70px 50px;
    padding-left: 250px;
    margin-left: 150px;
    width: calc(100% - 150px);
    height: 100%;
    box-shadow: 0 50px 50px rgba(0, 0, 0, 0.25);
    /* background-color: var(--black-77); */
}

.contact .box-contanier .contact-form h2 {
    font-size: 24px;
    font-weight: 500;
    color: var(--heading-color);
}

.contact .box-contanier .contact-form .form-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 30px;
}

.contact .box-contanier .contact-form .form-box .input-box {
    position: relative;
    margin-bottom: 35px;
}

.contact .box-contanier .contact-form .form-box .input-box.w-50 {
    width: 47% !important;
}

.contact .box-contanier .contact-form .form-box .input-box.w-100 {
    width: 100%;
}

.contact .box-contanier .contact-form .form-box .input-box input,
.contact .box-contanier .contact-form .form-box .input-box textarea {
    width: 100%;
    resize: none;
    padding: 5px 0;
    font-size: 18px;
    font-weight: 300;
    color: var(--black-to-second);
    border: none;
    outline: none;
    border-bottom: 1px solid #777;
    background-color: var(--background-white);
}

.contact .box-contanier .contact-form .form-box .input-box textarea {
    height: 120px;
}

.contact .box-contanier .contact-form .form-box .input-box span {
    position: absolute;
    left: 0;
    padding: 5px 0;
    pointer-events: none;
    font-size: 18px;
    font-weight: 300;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
    color: var(--black-to-second);
}

.contact .box-contanier .contact-form .form-box .input-box input:focus ~ span,
.contact .box-contanier .contact-form .form-box .input-box input:valid ~ span,
.contact .box-contanier .contact-form .form-box .input-box textarea:focus ~ span,
.contact .box-contanier .contact-form .form-box .input-box textarea:valid ~ span {
    transform: translateY(-20px);
    -webkit-transform: translateY(-20px);
    -moz-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1px;
    color: var(--heading-color);
}

.contact .box-contanier .contact-form .form-box .input-box input[type="submit"] {
    position: relative;
    cursor: pointer;
    background-color: var(--heading-color);
    color: var(--black-77);
    border: none;
    max-width: 150px;
    padding: 12px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    font-weight: 500;
}

.contact .box-contanier .contact-form .form-box .input-box input[type="submit"]:hover {
    background-color: var(--background-white);
    color: var(--black-to-second);
}

/* ------------- Responsive ----------------- */
@media (max-width: 991px) {
    .contact .box-contanier {
        flex-direction: column-reverse;
        min-width: 720px;
        width: 100%;

    }

    .contact .box-contanier .contact-form {
        width: 100%;
        height: auto;
        position: relative;
        padding: 25px;
        margin-left: 0;
        box-shadow: none;
        border: 0.5px solid var(--heading-color);
    
    }

    .contact .box-contanier .contact-info {
        width: 100%;
        height: auto;
        width: 100%;
        height: auto;
        top: 0;
        box-shadow: none;
        flex-direction: row;
        position: relative;
    }

    .contact .box-contanier .contact-info ul.social {
        align-items: center;
    }

}

@media (max-width: 767px) {
    .contact .box-contanier {
        min-width: 545px;
    }

    .contact .box-contanier .contact-form {
        padding: 20px;
    }

    .contact .box-contanier .contact-info {
        flex-direction: column;
    }
}

@media (max-width: 576px) {
    .contact .box-contanier {
        min-width: 300px;
    }

    .contact .box-contanier .contact-form {
        display: none;
    }

    .contact .box-contanier .contact-info {
        flex-direction: column;
    }

}