.home {
    min-height: 100vh;
    padding: 150px 0;
    background-color: var(--main-color);
    background-size: cover;
    position: relative;
    overflow-x: hidden;
}

.home .home-text {
    z-index: 3;
    position: relative;
}

.home h1 {
    font-size: 40px;
    font-weight: 700;
    color: var(--white-color);
    margin: 0 0 20px;
}

.home h1 span {
    color: var(--black-900);
}

.home p {
    font-size: 16px;
    font-weight: 300;
    color: var(--white-color);
    line-height: 2;
}

.home .home-btn .btn-1 {
    margin-top: 5px;
}

.home .home-btn .video-play-btn i {
    line-height: 50px;
}

/* ------------- Responsive ----------------- */

@media (max-width: 576px) {
    .home h1 {
        font-size: 30px;
    }

    .home p,
    .section-title p {
        font-size: 13px;
    }

}