.team .profile-card {
    position: relative;
    width: 220px;
    height: 220px;
    background-color: var(--heading-color);
    padding: 30px;
    box-shadow: 0 0 22px #3336;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.team .profile-card:hover {
    border-radius: 10px;
    height: 260px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.team .profile-card .img {
    position: relative;
    width: 100%;
    height: 100%;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.team .profile-card:hover .img {
    transform: translateY(-60px);
    -webkit-transform: translateY(-60px);
    -moz-transform: translateY(-60px);
    -ms-transform: translateY(-60px);
    -o-transform: translateY(-60px);
}

.team .profile-card .img img {
    width: 100%;
    box-shadow: 0 0 22px #3336;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.team .profile-card:hover .img img{
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.team .profile-card .info {
    transform: translateY(-80px);
    -webkit-transform: translateY(-80px);
    -moz-transform: translateY(-80px);
    -ms-transform: translateY(-80px);
    -o-transform: translateY(-80px);
    padding: 15px 0;
    text-align: center;
    opacity: 0;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.team .profile-card:hover .info {
    opacity: 1;
}

.team .profile-card .info h3 {
    font-size: 18px;
    font-weight: bold;
    color: var(--black-77);
}

.team .profile-card .info p {
    font-size: 15px;
    font-weight: 300;
    color: var(--black-77);
    margin: 2px 0 6px 0;
}

.team .profile-card .info .social a {
    font-size: 18px;
    margin-right: 8px;
    color: var(--background-white);
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.team .profile-card .info .social a:hover {
    color: var(--black-900);
}

/* ------------- Responsive ----------------- */
@media (max-width: 576px) {
    .team .profile-card {
        margin: 0 auto 40px;
    }
}