.footer {
    background-color: var(--main-color);
    padding-top: 60px;
}

.footer .footer-col h3 {
    font-size: 20px;
    font-weight: 500;
    color: var(--white-color);
    margin: 0 0 25px;
}

.footer .footer-col h3:last-of-type {
    margin: 0 0 0px;
}

.footer .footer-col p {
    font-size: 16px;
    color: var(--white-bbb);
    font-weight: 300;
    line-height: 26px;
    margin: 0;
    padding: 1rem;
}

.footer .footer-col ul {
    margin: 0;
    padding: 1rem;
}

.footer .footer-col ul li:not(:last-child) {
    margin-bottom: 10px;
}

.footer .footer-col ul li a {
    font-size: 16px;
    color: var(--white-bbb);
    font-weight: 300;
    display: block;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
    text-decoration: none;
}

.footer .footer-col ul li a:hover {
    padding-left: 5px;
    text-decoration: none;
}

.footer .copyright-text {
    margin: 50px 0 0 0;
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
    color: var(--white-color);
    font-weight: 500;
    border-top: 1px solid var(--white-bbb);
}