.about .about-item {
    box-shadow: var(--shadow-black-100);
    margin: var(--m-15);
    padding: 25px;
    text-align: center;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.about .about-item::before,
.about .about-item::after {
    z-index: -1;
    content: '';
    position: absolute;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
    opacity: 0;
}

.about .about-item::before {
    height: 20px;
    width: 20px;
    background-color: var(--color-1);
    left: -20px;
    top: 40%;
}

.about .about-item:hover:before {
    opacity: 1;
    left: 10px;
}

.about .about-item::after {
    height: 30px;
    width: 30px;
    background-color: var(--color-2);
    right: -30px;
    top: 80%;
}

.about .about-item:hover:after {
    right: -15px;
    opacity: 1;
}

.about .about-item .icon {
    height: 60px;
    width: 60px;
    margin: 0 auto 20px;
    text-align: center;
    font-size: 25px;
    color: var(--heading-color);
    position: relative;
    z-index: 1;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
}

.about .about-item .icon::before {
    content: '';
    position: absolute;
    left: 100%;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: var(--heading-color);
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
    z-index: -1;
    opacity: 0;
}

.about .about-item:hover .icon {
    font-size: 18px;
    color: var(--black-900);
}

.about .about-item:hover .icon::before {
    left: 0;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    opacity: 1;
}

.about .about-item .icon i {
    line-height: 60px;
}

.about .about-item h3 {
    font-size: 22px;
    margin: 0 0 20px;
    color: var(--black-to-second);
    font-weight: 500;
}

.about .about-item p {
    font-size: 16px;
    line-height: 26px;
    font-weight: 300;
    color: var(--black-to-second);
    margin: 0 0 15px;
}
