@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');

/* ---------- Global CSS ----------- */
:root {
    --font-family: 'Poppins', sans-serif;
    --main-color: #03a9f4;
    --heading-color: #03a9f4;
    --second-color: #ff7f57;
    --black-to-second: #000;
    --black-color: #333;
    --white-color: #fff;
    --disabled-color: #ffffff75;
    --disabled-color-2: #ff7f5780;
    --white-bbb: #fff;
    --black-900: #000;
    --black-77: #f7f7f7;
    --color-1: #e91e63;
    --color-2: #f5ae10;
    --background-white: #fff;
    --shadow-black-100: 0 5px 15px rgba(0, 0, 0, 0.1);
    --shadow-black-300: 0 5px 15px rgba(0, 0, 0, 0.3);
    --black-alpha-100: rgba(0, 0, 0, 0.05);
    --main-padding: 60px 0;
    --m-15: 15px;
    --p-30: 30px;
}

body {
    font-family: 'Poppins', sans-serif !important;
    font-size: 16px;
    font-weight: 400;
    background-color: var(--background-white);
    text-transform: capitalize;
    overflow-x: hidden !important;
}

body.dark {
    --white-color: var(--second-color);
    --white-bbb: var(--second-color);
    --heading-color: var(--second-color);
    --black-to-second: var(--second-color);
    --main-color: var(--black-color);
    --black-77: var(--black-color);
    --background-white: var(--black-color);
    --shadow-black-100: var(--shadow-black-300);
    --black-alpha-100: rgba(255, 255, 255, 0.05);
    --black-900: #fff;
    --disabled-color: var(--disabled-color-2);
}

html {
    scroll-behavior: smooth;
}

img {
    max-width: 100%;
    vertical-align: middle;
}

ul {
    list-style: none;
}

.btn-1 {
    background-color: var(--white-color);
    padding: 10px 25px;
    border: none;
    font-size: 16px;
    font-weight: 500;
    color: var(--main-color);
    border-radius: 30px;
    box-shadow: var(--shadow-black-300);
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
}

.btn-1:focus {
    box-shadow: var(--shadow-black-300);
}

.btn-1:hover {
    color: var(--white-color);
    background-color: var(--main-color);
}

.btn-2 {
    background-color: var(--main-color);
    padding: 10px 25px;
    border: none;
    font-size: 16px;
    font-weight: 500;
    color: var(--color-fff);
    border-radius: 30px;
    box-shadow: var(--shadow-black-100);
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    -ms-border-radius: 30px;
    -o-border-radius: 30px;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
}

.btn-2:focus {
    box-shadow: var(--shadow-black-100);
}

.btn-2:hover {
    color: var(--main-color);
    background-color: var(--color-fff);
}

.section-padding {
    padding: var(--main-padding);
}

.section-title { 
    margin-bottom: 30px;
}

.section-title h2 {
    font-weight: 700;
    font-size: 35px;
    color: var(--black-900);
    text-align: center;
    position: relative;
    margin: 0;
}

.section-title h2 span {
    color: var(--heading-color);
}

.section-title h2::before {
    content: '';
    position: absolute;
    height: 4px;
    width: 20%;
    bottom: -10px;
    left: 40%;
    background-color: var(--heading-color);
}

.section-title p {
    text-align: center;
    margin: 30px 0;
    font-weight: 300;
    font-size: 15px;
    color: var(--black-to-second);
}

@media (max-width: 576px) {
    .section-title h2 {
        font-size: 30px;
    }

    .section-title p {
        font-size: 13px;
    }
}