* {
    padding:0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-family: var(--font-family);
}

.navbarBg {
    background-color: var(--main-color);
    height: 100vh;
}

.preloader {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 2100;
    background-color: var(--main-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

.preloader span {
    display: block;
    height: 60px;
    width: 60px;
    background-color: var(--white-color);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    animation: zoomInOut_01 1s ease infinite;
    -webkit-animation: zoomInOut_01 1s ease infinite;
}

@keyframes zoomInOut_01 {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
        -moz-transform: scale(0);
        -ms-transform: scale(0);
        -o-transform: scale(0);
    }

    100% {
        opacity: 0;
        transform: scale(1);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
    }
}

/* ------------- Toggle Them - Light and Dark ------------------- */
.toggle-them {
    position: fixed;
    right: 0;
    top: calc(50% - 20px);
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    box-shadow: var(--shadow-black-300);
    background-color: var(--heading-color);
    z-index: 1200;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    text-align: center;
    transition: .5s;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
}

.toggle-them:hover {
    background-color: var(--black-900);
}

.toggle-them i {
    line-height: 40px;
    color: var(--black-77);
}

/*========== SCROLL UP ==========*/
.scrollup {
    position: fixed;
    right: 0;
    bottom: 35px;
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    box-shadow: var(--shadow-black-300);
    background-color: var(--heading-color);
    z-index: 1200;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    text-align: center;
    opacity: 0;
    display: none;
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
}

.scrollup:hover {
    background-color: var(--black-900);

}

.scrollup__icon {
    line-height: 40px;
    color: var(--background-white);
}

/* --------------- Show scroll ------------- */
.show-scroll {
    opacity: 1;
    display: block;
}


